import React, { useState, useEffect } from 'react';
import * as S from './Header.styled';

const Header = () => {
  const [activeMenu, setActiveMenu] = useState(null);
  const [isScrolled, setIsScrolled] = useState(false);

  const handleMouseEnter = (menuNumber) => {
    setActiveMenu(menuNumber);
  };

  const handleMouseLeave = () => {
    setActiveMenu(null);
  };

  useEffect(() => {
    const handleScroll = () => {
      const scrollPosition = window.scrollY;
      setIsScrolled(scrollPosition > 0);
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);
  
  return (
    <S.Wrapper isScrolled={isScrolled} onMouseLeave={handleMouseLeave}>
      <S.Header isScrolled={isScrolled}>
      <S.Logo src='/img/Logo.png' alt='Logo' />
        <S.Menu>
          <S.Menu1 onMouseEnter={() => handleMouseEnter(1)}></S.Menu1>
          <S.Menu2 onMouseEnter={() => handleMouseEnter(2)}></S.Menu2>
          <S.Menu3 onMouseEnter={() => handleMouseEnter(3)}></S.Menu3>
          <S.Menu4 onMouseEnter={() => handleMouseEnter(4)}></S.Menu4>
          <S.Menu5 onMouseEnter={() => handleMouseEnter(5)}>CRO </S.Menu5>
          <S.Menu6 onMouseEnter={() => handleMouseEnter(6)}></S.Menu6>

          
          {activeMenu && (
            <S.MenuBar>
              {activeMenu === 1 && (
                <S.MenuStyle1>
                  <S.SubMenu1 >CEO </S.SubMenu1>
                  <S.SubMenu2 > </S.SubMenu2>
                  <S.SubMenu3 >CI </S.SubMenu3>
                  <S.SubMenu4 ></S.SubMenu4>
                  <S.SubMenu5 ></S.SubMenu5>
                  <S.SubMenu5 >Open Innovation</S.SubMenu5>
                  <S.SubMenu5 ></S.SubMenu5>
                </S.MenuStyle1>
              )}
              {activeMenu === 2 && (
                <S.MenuStyle2>
                  <S.SubMenu6 > </S.SubMenu6>
                  <S.SubMenu7 >  </S.SubMenu7>
                </S.MenuStyle2>
              )}           
              {activeMenu === 3 && (
                <S.MenuStyle3>
                  <S.SubMenu8 ></S.SubMenu8>
                  <S.SubMenu9 ></S.SubMenu9>
                  <S.SubMenu10 ></S.SubMenu10>
                  <S.SubMenu11 ></S.SubMenu11>
                </S.MenuStyle3>
              )}
              {activeMenu === 4 && (
                <S.MenuStyle4>
                  <S.SubMenu12 ></S.SubMenu12>
                  <S.SubMenu13 ></S.SubMenu13>
                </S.MenuStyle4>
              )}
              {activeMenu === 5 && (
                <S.MenuStyle5>
                  <S.SubMenu14> </S.SubMenu14>
                  <S.SubMenu15> </S.SubMenu15>
                </S.MenuStyle5>
              )}
              {activeMenu === 6 && (
                <S.MenuStyle6>
                  <S.SubMenu16></S.SubMenu16>
                  <S.SubMenu17></S.SubMenu17>
                  <S.SubMenu18></S.SubMenu18>
                  <S.SubMenu19></S.SubMenu19>
                </S.MenuStyle6>
              )}
            </S.MenuBar>
          )}
        </S.Menu>
      </S.Header>
    </S.Wrapper>
  );
};

export default Header;