import React, { useState, useEffect } from 'react';
import YouTube from 'react-youtube';
import * as S from './Main.styled';
import axios from 'axios';

// const backendBaseUrl = 'http://umust302.ap-northeast-2.elasticbeanstalk.com';

function formatDate(dateTimeString) {
  const parsedDate = new Date(dateTimeString);
  const formattedDate = parsedDate.toISOString().split('T')[0];
  return formattedDate;
}



const images = [
  '/img/slide_01.png',
  '/img/slide_02.png',
  '/img/slide_03.png',
];

const videoId = 'HJ7bus70-_A'; //   ID

export default function Main() {
  const [currentImage, setCurrentImage] = useState(0);
  const [isMouseOver, setIsMouseOver] = useState(false);
  const [touchStartX, setTouchStartX] = useState(null);
  /* API  */
  const [press, setPress] = useState([]); /*  */
  
  const [events, setEvents] = useState([]); /*  */
  

  useEffect(() => {
    let isMounted = true; 


    const fetchPress = async () => {
      try {
        const response = await axios.get('/api/articles/NEWS/latest');
        setPress([response.data]);
        console.log()
    
      } catch (error) {
        console.error('   :', error);
      }
    };
  


const fetchEvents = async () => {
  try {
    const response = await axios.get('/api/articles/EVENT/latest');
    setEvents([response.data]);

  } catch (error) {
    console.error('   :', error);
  }
};


  
    fetchPress();
    fetchEvents();
  
    return () => {
      isMounted = false;
    };
  }, []);
  

  useEffect(() => {
    const interval = setInterval(() => {
      if (!isMouseOver) {
        setCurrentImage((prevImage) => (prevImage + 1) % images.length);
      }
    }, 4000);

    return () => clearInterval(interval);
  }, [isMouseOver]);

  const handleMouseEnter = () => {
    console.log(' ');
    setIsMouseOver(true);
  };

  const handleMouseLeave = () => {
    console.log(' ');
    setIsMouseOver(false);
  };

  const handleTouchStart = (e) => {
    console.log(' ', e.touches[0]);
    setTouchStartX(e.touches[0].clientX);
  };

  const handleTouchMove = (e) => {
    if (touchStartX !== null) {
      const touchEndX = e.touches[0].clientX;
      const distance = touchEndX - touchStartX;
      console.log(' , :', distance, ' :', e.touches[0]);

      if (distance > 50) {
        setCurrentImage((prevImage) => (prevImage - 1 + images.length) % images.length);
      } else if (distance < -50) {
        setCurrentImage((prevImage) => (prevImage + 1) % images.length);
      }

      setTouchStartX(null);
    }
  };

  const handleTouchEnd = () => {
    console.log(' ');
    setTouchStartX(null);
  };

  return (
    <S.Wrapper>
      <S.ImageSlide
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
        onTouchStart={handleTouchStart}
        onTouchMove={handleTouchMove}
        onTouchEnd={handleTouchEnd}
      >
        {images.map((image, index) => (
          <S.SlideImage
            key={index}
            src={image}
            style={{
              transform: `translateX(-${currentImage * 100}%)`,
              filter: isMouseOver ? 'brightness(0.9)' : 'none',
            }}
          />
        ))}
        <S.SlideBtn src='/img/slide_btn.png' />
      </S.ImageSlide>

      {/*    */}
      <S.Section1>
        <S.Container>
          <YouTube videoId={videoId}
           opts={{ width: '600px', height: '300px', border: '3px solid #000' }} />

           <S.Title> TV 
              <S.Text1><br/>[ ]"    150 "</S.Text1>
              <S.Text2><br/> |  x-       
                      <br />      .
                      <br />       .
                      <br />  10%~15%     .
              </S.Text2>
              <S.MoreBtn src='/img/more_btn.png'/>{/*     */}
           </S.Title>

        </S.Container>
      </S.Section1>


    {/* Section2 */}
      <S.Section2>
        <S.Icontainer>
          <S.IconContainer>
              <S.Tech src='/img/Tech.png'/>
              <S.TectTitle></S.TectTitle>
          </S.IconContainer>

          <S.IconContainer>
              <S.Thesis src='/img/Thesis.png'/>
              <S.ThesisTitle></S.ThesisTitle>
          </S.IconContainer>

          <S.IconContainer>
              <S.Authentication src='/img/Authentication.png'/>
              <S.AuthenticationTitle></S.AuthenticationTitle>
          </S.IconContainer>

          <S.IconContainer>
              <S.Research src='/img/service.png'/>
              <S.ResearchTitle></S.ResearchTitle>
          </S.IconContainer>

          <S.IconContainer>
              <S.Notice src='/img/notice.png'/>
              <S.NoticeTitle></S.NoticeTitle>
          </S.IconContainer>

          <S.IconContainer>
              <S.Map src='/img/map.png'/>
              <S.MapTitle> </S.MapTitle>
          </S.IconContainer>
        </S.Icontainer>
      </S.Section2>

 
      <S.Section3>
        <S.Section3Left>
            <S.LeftContent>
              <S.Medical src='/img/Medical.png'/>
              <S.Cosmetic src='/img/Cosmetic.png'/>

              
            </S.LeftContent>
            <S.TextContainer>
              <S.Text>RECENT NEWS</S.Text>

                {/*  */}
                <S.PressContainer>
                  {press.map((item, index) => (
                    <S.Press key={index}>
                      <div>{item.title.length > 10 ? `${item.title.substring(0, 10)}...` : item.title}</div>
                      <div>{formatDate(item.createdAt)}</div>
                      <div>{item.content.length > 60 ? `${item.content.substring(0, 60)}...` : item.content}</div>
                    </S.Press>
                  ))}
                </S.PressContainer>

                {/*  */}
                <S.EventContainer>
                  {events.map((item, index) => (
                    <S.Event key={index}>
                      <div>{item.title.length > 10 ? `${item.title.substring(0, 10)}...` : item.title}</div>
                      <div>{formatDate(item.createdAt)}</div>
                      <div>{item.content.length > 60 ? `${item.content.substring(0, 60)}...` : item.content}</div>
                    </S.Event>
                  ))}
                </S.EventContainer>


            </S.TextContainer>

        </S.Section3Left>

        <S.Section3Right>
            <S.RightContent>
              <S.Album src='/img/Album.png'/>
            </S.RightContent>
        </S.Section3Right>
      </S.Section3>

      
    </S.Wrapper>
  );
}
